.form__wrapper {
    width: 30%;
    margin: auto;
}

.search {
    height: 40px;
    background: #444;
    background: rgba(0, 0, 0, .2);
    border: 1px solid #fff;
}

.search input {
    width: 80%;
    padding: 10px 5px;
    text-align: center;
    color: #ccc;
    border: 0;
    background: transparent;
    border-right: 1px solid white;
    border-radius: 0;
}

.search input:focus {
    outline: 0;
    background: transparent;
    border-radius: 0;
}

.search button {
    height: 38px;
    width: 20%;
    border: 0;
    padding: 0;
    cursor: pointer;
    color: #fff;
    background: transparent;
}

.search button:hover {
    background: #fff;
    color: #444;
    overflow: hidden;
}

.search button:active {
    box-shadow: 0px 0px 12px 0px rgba(225, 225, 225, 1);
}

.search button:focus {
    outline: 0;
}

/* MEDIA QUERIES */

@media (max-width: 1200px) {
    .form__wrapper {
        width: 50%;
    }
}

@media (max-width: 600px) {
    .form__wrapper {
        width: 80%;
    }
}
@media (max-width: 400px) {
    .form__wrapper {
        width: 90%;
    }
}

.fade-appear {
    opacity: 0.01;
}

.fade-appear.fade-appear-active {
    opacity: 1;
    transition: opacity 700ms ease-in;
}
article {
    width: 22%;
}

/* MEDIA QUERIES */

@media (max-width: 600px) {
    article {
        width: 80%;
    }
}

@media  (min-width: 600px) and (max-width: 1200px) {
    article {
        width: 50%;
    }
}

@media  (min-width: 1200px) and (max-width: 1400px) {
    article {
        width: 40%;
    }
}



/* MEDIA QUERIES */

@media (max-width: 850px) {
    .country_wrapper {
        height: auto !important;
        width: 90% !important;
    }
    .country__song-line {
        -webkit-flex-wrap: wrap;
                flex-wrap: wrap;
        width: 80%;
        -webkit-justify-content: center;
                justify-content: center;
        margin-left: 2rem !important;
    }
    .country__line-breaker {
        display: inline-block;
        width: 100%;
        margin-bottom: 20px;
        text-align: center;
    }
}

.overlay {
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 2;
}

.popup {
    position: absolute;
    width: 70%;
    height: 50%;
    top: 50%;
    left: 50%;
    font-size: 50px;
    color: white;
    background-color: white;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
}

.close {
    position: absolute;
    top: -10px;
    right: -10px;
    background-color: white;
    width: 26px;
    height: 26px;
    border-radius: 50%;
    cursor: pointer;
    border: 1px solid #cfcece;
    font-size: 24px;
    color: grey;
    line-height: 0.9;
}
/* TYPOGRAPHY */

.App {
  text-align: center;
  font-family: 'Lato', sans-serif;
}

h1 {
  font-size: 5rem;
  background: -webkit-linear-gradient(left, red, #e4b8b7);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-family: 'Montserrat', sans-serif;
  font-weight: 900;
}

h2 {
  font-size: 3rem;
  font-weight: 100;
}

h2 span {
  background-color: #333333;
  padding: 5px 15px 10px 15px;
}

h3 {
  font-size: 3rem;
  color: pink;
  font-weight: 100
}

/* MEDIA QUERIES */

@media (max-width: 1200px) {
  h1 {
    font-size: 4rem;
  }

  h2,
  h3,
  #specific-country {
    font-size: 2.5rem !important;
  }
}

@media (max-width: 600px) {
  h1 {
    font-size: 3.5rem;
  }

  h2,
  h3,
  #specific-country {
    font-size: 2rem !important;
  }
}

/* ROTATE EFFECT */

.hvr-icon {
  transition-duration: 1s;
  transition-property: -webkit-transform;
  transition-property: transform;
  transition-property: transform, -webkit-transform;
  transition-timing-function: ease-in-out;
}

.hvr-icon:hover {
  -webkit-transform: rotate(360deg);
  transform: rotate(360deg);
}

/* GROW ON HOVER EFFECT */

.grow-hov:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

/* GREY BACKGROUND HOVER EFFECT */

.grey-back:hover {
  background-color: #e9e9e9;
}

/* ELEMENT INSIDE DIV NEVER A TARGET FOR POINTER EVENTS EFFECT */

.fav-wrapper>* {
  pointer-events: none;
  cursor: pointer;
}

.fav-wrapper {
  cursor: pointer;
}
